 .gpt3__cta{
    border: 1px solid white;
    color: #000;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    padding: 2rem;
    margin: 4rem;

    border-radius: 1rem;
    background: var(--gradient-bar);
 }

 .gpt3__cta-content {
    font-family: var(--font-family);
    color: #0E0E0E;
    font-size: 12px;
    font-weight: 500;
    line-height: 30px; /* 250% */
    
 }

 .gpt3__cta-content h3 {
    color: #000;
    font-family: var(--font-family);
    font-size: 24px;
    font-weight: 800;
    line-height: 45px;
 }

 .gpt3__cta-btn {
    
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 2rem;


 }

 .gpt3__cta-btn button {
    border-radius: 40px;
    background: #000;
    color: #FFF;
    font-family: var(--font-family);
    font-size: 18px;
    font-style: normal;
    font-weight: 700;
    line-height: 30px; /* 166.667% */
    padding: 0.5rem 1rem;

    border: none;
    outline: none;
    cursor: pointer;
    max-width: 150px;

 }


 @media screen and (max-width: 650px) {
   .gpt3__cta{
      flex-direction: column;
   }
   
   .gpt3__cta-btn{
      margin: 2rem 0 0;
   }

 }

 @media screen and (max-width: 550px) {
   .gpt3__cta {
      margin: 4rem 2rem;
   }

   .gpt3__cta-content {
      font-size: 18px;
      line-height: 32px;
   }

   .gpt3__cta-btn button {
      font-size: 14bx;
      line-height: 28px;
   }

 }