.gpt3__footer {

    display: flex;
    color: white;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    background: var(--color-footer);

}

.gpt3__footer-heading {
    width: 100%;
    text-align: center;
    margin-bottom: 3rem;
}

.gpt3__footer-heading h1 {
    text-align: center;
    font-family: var(--font-family);
    font-size: 62px;
    font-style: normal;
    font-weight: 800;
    line-height: 75px; /* 120.968% */
    letter-spacing: -2.48px;
}

.gpt3__footer-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1rem;
    border: 1px solid #FFF;
    text-align: center;
    margin-bottom: 10rem;
    cursor: pointer;
}

.gpt3__footer-btn p {

    color: #FFF;
    font-family: var(--font-family);
    font-size: 18px;
    word-spacing: 2px;
    font-weight: 400;
    line-height: normal;
}

.gpt3__footer-links {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    flex-direction: row;

    width: 100%;
    text-align: left;

}

.gpt3__footer-links div {
    width: 250px;
    margin: 1rem;
}

.gpt3__footer-links_logo {
    display: flex;
    flex-direction: column;
}

.gpt3__footer-links_logo img {
    width: 118px;
    height: 30px;
    flex-shrink: 0;
    margin-bottom: 1rem;
}

.gpt3__footer-links_logo p {
    color: #FFF;
    font-family: var(--font-family);
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;  
}

.gpt3__footer-links_div {
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
}

.gpt3__footer-links_div h4 {
    color: #FFF; 
    font-family: var(--font-family);
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    margin-bottom: 1rem;
}

.gpt3__footer-links_div p {
    color: #FFF;
    font-family: var(--font-family);
    font-size: 12px;
    font-weight: 400;
    line-height: normal;
    margin-bottom: .5rem;
    cursor: pointer;
    
}

.gpt3__footer-copyright {
    margin-top: 2rem;
    text-align: center;
    width: 100%;
}

.gpt3__footer-copyright p {
    color: #FFF;
    font-family: var(--font-family);
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;

    }


@media screen and (max-width: 850px) {

    .gpt3__footer-heading h1 {
        font-size: 44px;
        line-break: 50px;
    }
    
}

@media screen and (max-width: 550px) {

    .gpt3__footer-heading h1 {
        font-size: 34px;
        line-break: 42px;
    }

    .gpt3__footer-links div{
        margin: 1rem 0;
    }

    .gpt3__footer-links p{
        font-size: 14px;
        line-break: 20px;
    }
    
}

@media screen and (max-width: 550px) {

    .gpt3__footer-heading h1 {
        font-size: 27px;
        line-break: 38px;
    }
}